.App .links-container{display:flex;flex-direction:column;align-content:space-between}.App .links-container .flex-end{display:flex;flex-direction:column;justify-content:flex-end}.App .about-background{margin:10vh 0;position:relative}@media (min-width: 992px){.App .about-background{margin:30vh 0}}.App .about-background:before{content:'';display:block;background:#fff;position:absolute;border-radius:24px;box-shadow:0px 3px 23px 0px rgba(0,0,0,0.1);width:calc(100% - 30px);height:50%;bottom:-10px}@media (min-width: 992px){.App .about-background:before{right:-30px;top:-50px;width:calc(50vw + 60px);height:calc(100% + 100px);border-top-left-radius:50px;border-bottom-left-radius:50px}}

.header{position:relative;background-color:#fff;min-height:100vh;display:flex}@media (max-width: 767.98px){.header{margin-bottom:40px}}@media (max-height: 704px){.header .button-scroll{display:none}}@media (max-width: 767.98px){.header .button-scroll{display:none}}@media (min-width: 768px) and (min-height: 667px){.header .button-scroll-container{position:absolute;top:calc(100vh - 100px);left:calc(50vw - 20px)}.header .button-scroll-container .button-scroll{position:relative;display:block;margin:0 auto;border:2px solid #cc0e74;width:40px;height:60px;border-radius:20px;cursor:pointer}.header .button-scroll-container .button-scroll .circle{position:absolute;top:10px;left:calc(50% - 4px);width:8px;height:8px;display:block;background:#cc0e74;border-radius:50%;-webkit-animation:scroll 2s ease-in-out infinite;animation:scroll 2s ease-in-out infinite}.header .button-scroll-container .button-scroll:hover .circle{-webkit-animation:scroll-hover 2s infinite;animation:scroll-hover 2s infinite}@-webkit-keyframes scroll{0%{opacity:0}40%{opacity:1}90%{transform:translateY(12px);opacity:0}100%{opacity:0}}@keyframes scroll{0%{opacity:0}40%{opacity:1}90%{transform:translateY(12px);opacity:0}100%{opacity:0}}@-webkit-keyframes scroll-hover{0%{transform:translateY(0)}50%{transform:translateY(12px)}100%{transform:translateY(0)}}@keyframes scroll-hover{0%{transform:translateY(0)}50%{transform:translateY(12px)}100%{transform:translateY(0)}}}.header-container{display:flex;align-items:center}.header .social-link svg{height:28px;margin-right:20px;margin-bottom:20px;fill:#393b44;transition:.2s ease}.header .social-link svg:hover{fill:#000}.header p{font-size:28px}@media (max-width: 767.98px){.header .intro-header{font-size:46px;margin:40px 0}}.header:before{content:'';display:block;position:absolute;top:0;width:100vw;height:100vh;background:#fbf4f9}

.button{box-sizing:border-box;position:relative;overflow:hidden;margin:12px 0;z-index:1;height:60px;display:inline-block;text-align:center;cursor:pointer;border-radius:50px;font-size:24px;padding:10px 24px;width:100%}@media (min-width: 768px){.button{max-width:400px;margin:20px 20px 20px 0;width:auto}}.button.primary{background-color:#cc0e74;color:#fff}.button.secondary{border:2px solid #cc0e74;color:#cc0e74}.button.secondary:after{content:'';position:absolute;bottom:0;left:0;width:100%;height:100%;border-radius:10rem;z-index:-2}.button.secondary:before{content:'';position:absolute;bottom:0;left:0;width:0%;height:100%;background-color:#cc0e74;transition:all .3s;border-radius:10rem;z-index:-1}.button.secondary:hover{color:#fff}.button.secondary:hover:before{width:100%}.button.secondary:hover.download svg{transition:.24s ease;fill:#fff}.button.small{font-size:18px;height:40px;min-width:-webkit-max-content;min-width:-moz-max-content;min-width:max-content;padding:4px 8px}@media (min-width: 768px){.button.small{max-width:124px}}.button:hover{text-decoration:none}

.about{margin-bottom:40px}@media (min-width: 768px){.about{padding-right:80px;margin-bottom:0}}.about h3,.about p{position:relative}.about h3{padding-bottom:24px}.about:before{content:'';position:absolute;z-index:-1;left:-300px;display:block;width:100%;height:65vh;background:url(/bv-com/static/media/blob.6f7b1a09.svg) no-repeat}

.contact{min-height:400px;position:relative}@media (max-width: 991px){.contact{padding-top:40px}}.contact a{display:block}.contact a:hover{color:#cc0e74}.contact .contact-btn-row{align-items:flex-end}@media (max-width: 767.98px){.contact .contact-btn-row{font-size:16px}}.contact .contact-btn-row a{text-decoration:none}.contact .contact-btn-row svg{margin:0 20px 0 0}.contact .contact-btn-row .button:last-child{margin-bottom:0}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
